.comment-box {
  width: 100%;
}

.textarea-container {
  position: relative;
  display: inline-block;
  width: 100%;
}

textarea {
  width: 100%;
  padding-right: 2em;
  /* space for the emoji icon */
  box-sizing: border-box;
  border: none;
  background: none;
  border-bottom: 1px solid #ccc;
}

.emoji-icon {
  position: absolute;
  /* top: 50%; */
  bottom: 0;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
  color: #888;
  font-size: 25px;
}

.emoji-picker {
  position: absolute;
  bottom: 45px;
  /* Adjust as needed */
  right: 10px;
  z-index: 10;
}